.robopot-wrap .title-wrap li:before { display: block; content: ""; position: absolute; top: 9px; left: 2px; width: 7px; height: 2px; background: #aeaeae; }

.robopot-wrap img { display: block; }

.robopot-wrap .robopot-info { margin-bottom: 10px; }

.robopot-wrap .robopot-info dl { margin-top: 40px; }

.robopot-wrap .robopot-info dt { color: #222; font-size: 17px; font-weight: bold; letter-spacing: -1px; line-height: 1.2; margin-bottom: 17px; }

.robopot-wrap .robopot-info p { line-height: 1.7; }

.robopot-wrap .box-notice { margin-top: 40px; }

.section-report { width: 1000px; margin: 0 auto; min-width: 1000px; }

.section-report .main-contents { padding-top: 37px; }

.section-report .main-contents:after { display: block; content: ""; clear: both; }

.section-report .select-box-wrap { margin-top: 30px; }

.section-report .select-box-wrap .icon { padding: 9px 4px 0 10px; }

.section-report .board-contents { padding-top: 41px; }

.section-report .board-contents .number { margin-right: 1px; }

.section-report .board-contents .table-new { position: relative; top: 2px; }

.section-report .board-contents tr td:last-child { padding: 14px 10px; text-align: center; }

.section-report .box-notice { float: left; border: 0; padding: 0; margin: 37px 0 1px 0; }

.section-report .box-notice-heading { padding-left: 17px; margin-bottom: 15px; }

.section-report .box-notice li { background: 0; font-size: 11px; padding-left: 15px; line-height: 1.6; margin-bottom: 7px; }

.section-report .box-notice a { font-size: 0; margin-left: 8px; display: inline-block; vertical-align: bottom; }

.snb-report .title { background: url("../images/sub_report/lnb_title_0000001086.gif") no-repeat; }

.snb-report .depth1-link { line-height: 16px; }

.report-title { width: 218px; height: 34px; background: url("../images/sub_report/bbs_title_0000001087.gif") no-repeat; }

.report-banner { float: left; padding-top: 28px; }

.report-banner a { display: block; height: 100%; }

.report-banner img { display: block; }
